import React from 'react';

function About() {
    return (
        <>
            <h1>About</h1>
            <p>
            随着信息化浪潮的发展，如何通过技术或者设备手段得到设备的详细定位是信息化发展的重要课题。
            很多的设备、人员、物品都需要进行定位来确定其空间位置，在定位系统确切了解了设备、人员、物品的位置后，就可以按照既定的目的，对人员、物品、设备进行指挥、监控、搜集信息，并将其确切的位置上传到云端进行下一步的处理。定位拥有多种手段和方式，例如在室外厂商通常使用GPS、北斗导航、伽利略、格洛纳斯等GNSS（GLOBAL NAVIGATION SATELLITE SYSTEM）系统进行物品的定位，GPS、北斗等卫星定位方式具有性能好、全天候、精度高、应用广、产业配套齐全、自动测量的特点，是最好的物联网定位方式之一。但是GPS、北斗等卫星定位系统的卫星信号由于建筑物的阻拦导致室内信号差，通常只能用于室外，因此必须使用室内定位技术，最终定位物品当前所处的位置，室内定位通过也拥有多种手段、例如RFID、WIFI、蓝牙等手段，例如室内定位中可以使用蓝牙低功耗定位技术，其实现原理基于RSSI（Received Signal Strength Indicator），手段是通过在室内安装适当的网关设备，待定位设备被动地扫描获取已知位置的蓝牙网关发送的beacon信息，通过判断蓝牙网关发送的蓝牙信号的强度高低，设备就可以得到了自己与已经位置的信标的相对距离，从而就可以推导出自己的位置信息。BLE蓝牙定位技术具有精度高、体积小、低耗电、造价低、不易受干扰的特点，同时随着蓝牙技术发展，例如蓝牙5.1，其定位精度、信号强度都会不断变好，以更好的服务于物联网服务。随着物联网技术不断的创新与进步，硬、软件的不断完善，室内定位应用领域正在不断地开拓，其使用场景越来越多样化、普及化，如今已遍及国民经济各种部门，并开始逐步深入人们的日常生活。
            </p>
            <p>
            卫星定位具有性能好、全天候、精度高、应用广、产业配套齐全、自动测量的特点，但是也有其固有的缺点，即卫星信号很难突破建筑物的阻挡，因此未来解决卫星信号到达地面信号弱无法用于定位的困境，就必须要使用室内定位技术。室内定位拥有多种解决方式，例如采取无线通信、基站定位、惯导定位、动作捕捉等手段，WIFI技术通过测量路由器与设备之间的WIFI型号的RSSI以及信号角度从而得出设备位置，蓝牙技术类似通过蓝牙基站与设备之间信号的RSSI得出位置，蓝牙技术实现定位常用于小范围定位，例如楼层的某层或者某工厂，只要设备集成蓝牙芯片且支持蓝牙4.0及以上协议，那么就能实现定位，其拥有低功耗、成本低的优点。红外线室内定位通过室内的红外线发射传感器接收待定位设备的红外特征，主要定位手段有被定位设备使用特定的红外反射标识，被红外照射后，反射至传感器来确定位置，其二是，通过多发射器和接收器编织红外无线网覆盖来定位，其拥有精度高的特点，但是只能视距内传输，容易被障碍物遮挡。RFID室内定位通过芯片射频实现定位，具有传输范围大、成本低的优点，但是也有其固有的缺点，例如安全保护不佳、体积大难以集成到手机或者其他小型设备中。动作捕捉技术，属于光学摄像定位，通过室内的摄像头捕捉空间中的设备，通过对待测量物体的光学距离、角度、参考物数据进行计算，从而得出物体的空间位置。综合考量了诸多室内定位技术，本文采取的室内定位方式是蓝牙RSSI定位，其拥有功耗低和成本低的特点。
            </p>
            <p>
            本文的主体结构可分为六个章节，各章节具体内容如下：
            第一章绪论。主要介绍项目的背景与意义，国家物联网技术发展的重视，定位技术的广阔前景，国内外对室外定位技术的研究方向与技术，室内定位技术有研究方向与技术以及本文为什么会使用基于RSSI的蓝牙定位技术，最后介绍本文的主要工作和本文的组织结构。 
            第二章系统技术介绍，介绍了本文的三个技术组成，其一是基于NRF52832的蓝牙信标和蓝牙设备模块和基于BC26的NBIOT物联网模块，其二是基于天翼物联网平台、Python SDK和Python Demo、以及LeanCloud后端，其三是基于JavaScript、HTML、CSS、React、Mobx等技术的Web前端界面。
            第三章系统的需求分析，以功能性需求和非功能性需求分析为基础，对系统的总体架构、技术架构、技术实现和测试方法进行了设计，确定了室内定位系统的主要的功能模块，对室内定位系统整体的运行原理进行了分析，以及确定系统的主要交互方式。 
            第四章系统设计，在满足需求的前提下，先是对将室内定位系统进行了架构和系统的设计与规划，分为了客户端详细设计与服务端详细设计，服务端设计分为前端页面的划分与后端主要功能模块地图管理、信标管理管理、设备管理、用户登录进行了详细设计，对各功能模块进行了类图、时序图和程序流程图的绘制；
            第五章系统的实现和测试。对室内定位系统核心功能模块的前端页面按照系统需求和系统设计进行实现、分析、展示，完成了用户登录注册、地图上传与保存、地图管理、定位展示、设备管理、信标管理等模块并对系统的关键技术进行部分代码的展示，最后从系统的功能性和非功能性进行测试。 
            第六章总结与展望，主要总结了BLE室内系统目前的研究成果与技术，介绍了三大组成部分，硬件部分、后端部分、软件部分，阐述了系统的部分应用场景。展望了系统目前的缺点，未来的改进方向，以及室内定位技术未来的广阔前景。
            </p>
        </>
    )
}

export default About;